const { api_url } = pda_gold_v3_data;
const restApiRoot = api_url.replace(/\/$/, '');

export const AXIOS_CONFIG = function() {
    return {
        baseURL: `${restApiRoot}/pda/v3/`,
        timeout: 10000, //milisecond
        headers: {
            'Cache-Control': 'no-cache, no-store, must-revalidate',
            'Pragma': 'no-cache',
            'Expires': '0',
            'X-WP-Nonce': pda_gold_v3_data.nonce
        }
    }
}

